<template>
  <div class="home-page">
    <dt-header></dt-header>
    <div class="bottom">
        <div class="bottom-main">
          <div class="bottom-content">
            <div class="left">
              <dt-side-nav></dt-side-nav>
            </div>
            <div class="right">
              <router-view></router-view>
            </div>
          </div>
        </div>
    </div>
  </div>
</template>
<script>
import DtHeader from '@/components/DtHeader'
import DtSideNav from '@/components/DtSideNav'

export default {
  name: 'home',
  components: {
    DtHeader, DtSideNav
  },
  data() {
    return { 
      leftScrollBar: false
    }
  },
  created(){
    this.getBaseOptions()
  },
  methods: {
    getBaseOptions () {
      this.$dt.getAjax('/sys-dict/codes/PRO_STAT+PRO_STAT+PRO_DOMAIN+PRO_ACTIVE_TYPE+PRO_RELUST_TYPE+OUTLINE_PRO_STAT+PRO_MAJOR+PRO_CER_TYPE+PRO_CER_TYPE+PRO_COMPANY_TYPE+PRO_DEGREE+PRO_TYPE+SEX+BUDGET_SUBJECT',{},(res)=>{
         this.$store.commit('setBaseOptions',res)
         this.$store.commit('setLoadingFlag',false)
      },()=>{
         this.$store.commit('setLoadingFlag',false)
      })
    }
  }
}
</script>
<style lang="less">
  .home-page{
    height: 100%;
    .bottom{
      width: 100%;
      box-sizing: border-box;
      height: 100%;
      overflow: hidden;
    }
    .bottom-main{
      position: relative;
      height: calc(100% - 84px);
      margin-top: 84px;
    }
    .bottom-content{
      height: 100%;
      padding-top: 30px;
      box-sizing: border-box;
    }
    .left{
      position: absolute;
      left: 30px;
      top: 30px;
      padding-right: 20px;
      overflow: hidden;
      height: 100%;
    }
    .right{
      padding-left: 334px;
      height: 100%;
      box-sizing: border-box;
      overflow: hidden;
    }
  }
</style>





