              <span @click="myBacklog()">我的待办</span>
            </template>
          </el-submenu>

          <!-- 项目基础信息 -->
          <el-submenu index="2">
            <template slot="title">
              <!-- <i class="el-icon-location"></i> -->
              <span>基础信息</span>
            </template>
            <el-menu-item-group>
              <el-menu-item index="/newslist">项目基础信息</el-menu-item>
              <el-menu-item index="/newslist">项目信息维护</el-menu-item>
              <el-menu-item index="/newslist">科研平台信息库</el-menu-item>
              <el-menu-item index="/newslist">评审专家信息库</el-menu-item>
              <el-menu-item index="/newslist">学术委员信息库</el-menu-item>
            </el-menu-item-group>
          </el-submenu>

          <!-- 项目申报 -->
          <el-submenu index="3">
            <template slot="title">
              <!-- <i class="el-icon-location"></i> -->
