            </template>
            <el-menu-item-group>
              <el-menu-item index="/projectQuery">项目查询</el-menu-item>
              <el-menu-item index="/projectApply">项目申报</el-menu-item>
              <el-menu-item index="/project-review">项目评审</el-menu-item>
              <el-menu-item index="/projectPublic">项目公示</el-menu-item>
							<el-menu-item index="/ProjectApproval">项目批复</el-menu-item>
            </el-menu-item-group>
          </el-submenu>

          <!-- 项目立项 -->
          <el-submenu index="4">
            <template slot="title">
              <!-- <i class="el-icon-location"></i> -->
              <span>项目立项</span>
            </template>
            <el-menu-item-group>
              <el-menu-item index="/assignmentBook">项目任务书</el-menu-item>
              <el-menu-item index="1-2">选项2</el-menu-item>
            </el-menu-item-group>
          </el-submenu>

          <!-- 项目执行 -->
