            </template>
            <el-menu-item-group>
              <el-menu-item index="1-1">选项1</el-menu-item>
              <el-menu-item index="1-2">选项2</el-menu-item>
            </el-menu-item-group>
          </el-submenu>

          <!-- 项目验收 -->
          <el-submenu index="6">
            <template slot="title">
              <!-- <i class="el-icon-location"></i> -->
              <span>项目验收</span>
            </template>
            <el-menu-item-group>
              <el-menu-item index="1-1">选项1</el-menu-item>
              <el-menu-item index="1-2">选项2</el-menu-item>
            </el-menu-item-group>
          </el-submenu>

          <!-- 项目评价 -->
          <el-submenu index="7">
            <template slot="title">
              <!-- <i class="el-icon-location"></i> -->
              <span>项目评价</span>
