              <!-- <i class="el-icon-location"></i> -->
              <span>题录</span>
            </template>
            <el-menu-item-group>
              <el-menu-item index="/new-title">新获项目</el-menu-item>
              <el-menu-item index="/award">奖励</el-menu-item>
              <el-menu-item index="/result-transform">成果转化</el-menu-item>
              <el-menu-item index="/result-test">成果鉴定</el-menu-item>
              <el-menu-item index="/patent">专利</el-menu-item>
              <el-menu-item index="/medical">新药器械</el-menu-item>
