<script>
export default {
  name: "DtSideNav",
  data() {
    return {
      leftScrollBar: false
    };
  },
  methods: {
    myBacklog() {
      this.$router.push("/myBacklog");
    }
  }
};
</script>
<style lang="less">
/* 侧边栏菜单 */
.navMenu {
  width: 244px;
  height: 100%;
  > .el-menu {
    width: 244px;
    box-sizing: border-box;
    border-right: 0;
    border: 1px solid #ccc;
    border-radius: 10px;
  }
  .el-submenu__title i {
    font-size: 16px;
  }
  .el-menu-item {
    padding-left: 38px;
    text-align: left;
  }
  /* 快捷导航 */
  .menu {
    padding: 20px 20px 0 20px;
    margin-bottom: 30px;
  }

  .el-menu-item-group ul li:last-child {
    margin-bottom: 20px;
  }
  /* 下拉箭头 */
  .el-icon-arrow-down:before {
    color: #fff;
  }
  /* 一级导航标题 */
  .el-submenu__title {
    background-color: #63b4ff;
    height: 36px;
    line-height: 36px;
    border-radius: 6px;
    font-size: 18px;
    font-weight: 700;
    color: #fff;
    letter-spacing: 1px;
    margin-bottom: 20px;
    padding-left: 38px !important;
  }

  .el-submenu__title:hover {
    background-color: #63b4ff;
  }
  .el-menu-item-group__title {
    padding: 0;
  }
  /* 二级导航 */
  .el-menu-item {
    padding: 0 !important;
  }
  .el-submenu .el-menu-item {
    text-align: center;
    height: 36px;
    line-height: 34px;
    min-width: 0;
  }
  .el-submenu .el-menu-item:hover {
  }

  .mainMenu-box {
    height: calc(100% - 98px);
    overflow: hidden;
    width: 100%;
    padding-right: 10px;
    .mainMenu-content {
      padding-bottom: 60px;
    }
    .mainMenu-content > .el-menu {
      width: 244px;
      box-sizing: border-box;
      // border:none;
      border: 1px solid #ccc;
      // border-right: none;
      border-radius: 10px;
    }
  }
  /* 主导航 */
  /* 主导航 一级导航 */
  .mainMenu {
    padding: 20px 20px 0 20px;
    box-sizing: border-box;
  }

  /* 一级导航标题 */
  .mainMenu .el-submenu__title {
    background-color: #5aae55;
    height: 34px;
    line-height: 30px;
    border-radius: 6px;
    font-weight: 700;
    color: #fff;
    letter-spacing: 1px;
    margin-bottom: 20px;
    padding-left: 38px !important;
    text-align: left;
    font-size: 18px;
  }

  .mainMenu .el-submenu__title:hover {
    // background-color: #5aae55;
    color: #fff;
  }
  .mainMenu .el-menu-item-group__title {
    padding: 0;
  }
  /* 二级导航 */
  .el-submenu .el-menu-item {
    height: 34px;
    line-height: 34px;
    padding-left: 38px !important;
    text-align: left;
    border: 1px solid transparent;
    border-radius: 4px;
    font-size: 16px;
    color: #696969;
  }
  .el-submenu .el-menu-item:hover {
    color: #63b4ff;
    background-color: transparent;
    border-color: #63b4ff;
  }
  .scroll {
    overflow: auto;
  }
}
</style>


